.playlist {
  max-width: 900px;
  width: 100%;
  margin: 0 auto;
  border-radius: 5px;
  border: 1px solid gainsboro;
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.title {
  font-weight: 600;
  font-size: 1.4rem;
}

.buttonFlex {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1em;
}

.btn {
  padding: 1em;
  background-color: white;
  border: 2px solid black;
  transition: all 0.1s linear;
}

.btn:hover {
  -webkit-box-shadow: 0px 0px 13px 10px rgba(128, 0, 255, 0.22);
  -moz-box-shadow: 0px 0px 13px 10px rgba(128, 0, 255, 0.22);
  box-shadow: 0px 0px 13px 10px rgba(128, 0, 255, 0.22);
  transition: all 0.2s linear;
}

.tracksBlock {
  display: flex;
  flex-direction: column;
  gap: 0.6em;
}
